.badges-showcase {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.badge {
    margin: 20px;
    width: 300px;
    border: 5px solid red;
    border-radius: 10px;
}

.badge-header {
    padding: 10px;
    background-color: red;
    color: white;
    font-family: "Courier New", Courier, monospace;
    font-weight: bold;
}

.badge-title {
    font-size: 25px;
    text-align: center;
}

.badge-subtitle {
    font-size: 15px;
    text-align: center;
}

.badge-body {
    padding: 10px;
    text-align: center;
    font-size: 22px;
}

.badge-footer {
    background-color: red;
    height: 10px;
}
